.App {
  text-align: center;
  height: 100vh;
  width: 100vw;
}

.bar-container-container {
  display: flex;
  height: 80%;
  width: 100%;
  background-color: darkslategrey;
  flex-direction: column;
}

.bar-container {
  display: flex;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  width: 99%;
  height: 96%;
  float: left;
}

.bar {
  margin-top: auto;
  height: 10px;
  width: 5px;

  margin-left: 2px;
  margin-right: 2px;
  background-color: skyblue;
  display: inline-block;
}

.menu {
  height: 20%;
  background-color: rgb(27, 59, 59);
  width: 100%;
  display: inline-flex;
}

.menu-left {
  width: 40%;
  display: block;
}

.menu-right {
  width: 70%;
  height: 100%;
  display: block;
}

.buttons {
  height: 40%;
}

.start-button {
  margin-right: 5px;
  margin-top: 1%;
  text-align: center;
  font-size: 24px;
  background-color: mediumseagreen;
  color: white;
  width: max(120px, 50px);
  height: 60%;
}

.stop-button {
  margin-right: 5px;
  margin-top: 1%;
  text-align: center;
  font-size: 24px;
  background-color: gray;
  color: white;
  width: max(120px, 50px);
  height: 60%;
}

.reset-button {
  margin-left: 5px;
  text-align: center;
  font-size: 24px;
  background-color: royalblue;
  color: white;
  width: max(120px, 50px);
  height: 60%;
}

.slider {
  opacity: 0.7;
}

.slider:hover {
  opacity: 1;
}

.slider-container {
  font-size: 24px;
  color: white;
  height: 35%;
}

.slider {
  appearance: none;
  width: 90%; /* Full-width */
  border-radius: 10px;
  background: #d3d3d3; /* Grey background */
  outline: none; /* Remove outline */
  opacity: 0.7; /* Set transparency (for mouse-over effects on hover) */
  transition: opacity 0.2s;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #1bd121;
  cursor: pointer;
}

.selector {
  font-size: 18px;
  height: 40%;
  width: max(250px, 110px);
}

.time {
  font-size: 24px;
  color: white;
  height: 20%;
}
